.box-sizing,.chk-box{box-sizing:border-box}.chk-box{width:13px;height:13px;outline:1px solid black;position:relative;margin-right:0;margin-left:auto;cursor:pointer;display:inline-block}.chk-box.disabled{opacity:0.5;cursor:default}.chk-box.checked::after{content:"";background-repeat:no-repeat;width:13px;height:13px;display:block}.chk-box.checked.GreenHook::after{background-image:url(images/GreenHook.svg)}.chk-box.checked.DefaultCross::after{background-image:url(images/DefaultCross.svg)}
